import React from 'react'; const items = [{ title:'S&P 500 Stock Prediction and Analysis', img:'/images/stock.jpeg', desc:'ML pipeline to forecast stock prices; regression, PCA, clustering.'},{ title:'Ambulance Routing System using Graph Algorithms', img:'/images/ambulance.webp', desc:'Dijkstra shortest-path routing with Flask backend.'},{ title:'Lung Disease Prediction using Logistic Regression', img:'/images/lungs.jpeg', desc:'Medical data classification; performance metrics and UI.'},];export default function Projects(){return (<div><h2 className="text-3xl font-bold mb-6 text-gray-900 dark:text-white">Projects</h2><div className="grid md:grid-cols-3 gap-6">{items.map(it=>(<article key={it.title} className="bg-white dark:bg-gray-800 rounded-lg shadow hover:scale-105 transform transition p-4"><img src={it.img} alt={it.title} className="w-full h-40 object-cover rounded" /><h3 className="mt-3 font-semibold text-lg text-gray-900 dark:text-white">{it.title}</h3><p className="text-sm text-gray-600 dark:text-gray-300 mt-2">{it.desc}</p><div className="mt-4 flex gap-2"><a className="px-3 py-1 bg-blue-600 text-white rounded" href="#">View</a><a className="px-3 py-1 border rounded text-sm" href="#">Source</a></div></article>))}</div></div>);}