import React from 'react'; const groups = {'Languages': ['Python','Java','SQL','JavaScript','HTML'],'Databases': ['MySQL','Oracle'],'Tools': ['VS Code','Jupyter Notebook','GitHub'],'Soft Skills': ['Collaboration','Scheduling','Coordination']};export default function Skills(){return (<div><h2 className="text-3xl font-bold mb-6 text-gray-900 dark:text-white">Skills</h2><div className="grid grid-cols-1 sm:grid-cols-2 gap-6">{Object.entries(groups).map(([k,v])=>(<div key={k} className="p-6 bg-gray-50 dark:bg-gray-900 rounded-lg shadow"><h3 className="font-semibold mb-3 text-gray-800 dark:text-gray-100">{k}</h3><div className="flex flex-wrap gap-3">{v.map(s=> <span key={s} className="px-3 py-1 rounded-full bg-blue-50 text-blue-700">{s}</span>)}</div></div>))}</div></div>);}